'use strict';

var React = require('react');
var ReactNative = require('react-native');
var {
  StyleSheet,
  TabBarIOS,
  Text,
  View,
  AppRegistry
} = ReactNative;

import UIExplorerApp from './jsx/UIExplorer/UIExplorerApp.web';

var img2_base64 = 'data:image/png;base64,';
var img1_base64 = 'data:image/png;base64,';

class TabBarExample extends React.Component {
  static title = '<TabBarIOS>';
  static description = 'Tab-based navigation.';
  static displayName = 'TabBarExample';

  state = {
    selectedTab: 'redTab',
    notifCount: 0,
    presses: 0,
  };

  _renderContent = (color: string, pageText: string, num?: number) => {
    return (
      <View style={[styles.tabContent, { backgroundColor: color }]}>
        <Text style={styles.tabText}>{pageText}</Text>
        <Text style={styles.tabText}>{num} re-renders of the {pageText}</Text>
      </View>
    );
  };

  render() {
    return (
      <TabBarIOS barTintColor="#fff"
      >
        <TabBarIOS.Item
          title="UIExplorer"
          icon={{ uri: img1_base64, scale: 3.5 }}
          >
          {this._renderView()}
        </TabBarIOS.Item>
        <TabBarIOS.Item
          icon={{ uri: img2_base64, scale: 3.5 }}
          title="Plugins"
          onPress={() => {
            this.setState({
              selectedTab: 'greenTab',
              presses: this.state.presses + 1
            });
          }}>
          {this._renderContent('#21551C', 'Green Tab', this.state.presses)}
        </TabBarIOS.Item>
      </TabBarIOS>
    );


  }

    _renderView(){
     
      return (<UIExplorerApp></UIExplorerApp>);
    }
}

var styles = StyleSheet.create({
  tabContent: {
    flex: 1,
    alignItems: 'center',
  },
  tabText: {
    color: 'white',
    margin: 50,
  },
});


AppRegistry.registerComponent('TabBarExample', () => TabBarExample);

var app = document.createElement('div');
document.body.appendChild(app);

AppRegistry.runApplication('TabBarExample', {
  rootTag: app
})

module.exports = TabBarExample;



